
.fade-slide-enter {
  opacity: 1;
  -webkit-transform: translateY(20px);
          transform: translateY(20px);
}
.fade-slide-enter-active {
  transition: all .5s;
}
.fade-slide-leave {
  opacity: 0;
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
}
.fade-slide-leave-active {
  opacity: 0;
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
  transition: all .5s;
}
@-webkit-keyframes scale {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
45% {
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
    opacity: 0.7;
}
80% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
}
@keyframes scale {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
45% {
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
    opacity: 0.7;
}
80% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
}
@-webkit-keyframes line-scale-pulse {
0% {
    -webkit-transform: scaley(1);
            transform: scaley(1);
}
50% {
    -webkit-transform: scaley(0.4);
            transform: scaley(0.4);
}
100% {
    -webkit-transform: scaley(1);
            transform: scaley(1);
}
}
@keyframes line-scale-pulse {
0% {
    -webkit-transform: scaley(1);
            transform: scaley(1);
}
50% {
    -webkit-transform: scaley(0.4);
            transform: scaley(0.4);
}
100% {
    -webkit-transform: scaley(1);
            transform: scaley(1);
}
}
.Lingxi__btn {
  outline: none;
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  border: 1px solid transparent;
  white-space: nowrap;
  line-height: 1.5;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding: 6px 15px;
}
.Lingxi__btn--error {
  color: #fff;
  background-color: #ed3f14;
  border-color: #ed3f14;
}
.Lingxi__btn--error:hover {
  color: #fff;
  background-color: #f16543;
  border-color: #f16543;
}
.Lingxi__btn--info {
  color: #fff;
  background-color: #2db7f5;
  border-color: #2db7f5;
}
.Lingxi__btn--info:hover {
  color: #fff;
}
.Lingxi__form {
  background: #f2f2f2;
  margin-bottom: 10px;
  padding: 6px;
  width: 484px;
}
.Lingxi__form__wrap {
  display: inline-block;
  width: 100%;
  position: relative;
  vertical-align: middle;
}
.Lingxi__form__item {
  margin: 6px;
  float: left;
}
.Lingxi__form__item--label {
  text-align: right;
  vertical-align: middle;
  float: left;
  font-size: 14px;
  color: #48576a;
  line-height: 1;
  padding: 11px 12px 11px 0;
  box-sizing: border-box;
}
.Lingxi__form__item--button {
  margin-top: 8px;
  margin-left: 6px;
}
.Lingxi__form__item--input {
  height: 32px;
  line-height: 1.5;
  font-size: 12px;
  border: 1px solid #dddee1;
  border-radius: 4px;
  background-color: #fff;
}
.Lingxi__mian {
  position: relative;
}
.Lingxi__table {
  width: 100%;
  max-width: 100%;
  margin-bottom: 20px;
  border-spacing: 0;
  border-collapse: collapse;
  border-top: 2px solid #42b983;
}
.Lingxi__table__loading {
  position: absolute;
  top: 40%;
  left: 30%;
}
.Lingxi__table th {
  min-width: 120px;
  padding: 10px 20px;
  background-color: #42b983;
  color: rgba(255, 255, 255, 0.66);
  -user-select: none;
}
.Lingxi__table td {
  min-width: 120px;
  padding: 10px 20px;
  text-align: center;
  border-bottom: 1px solid #dfe6ec;
}
.Lingxi__table tr {
  background-color: #fff;
}
.Lingxi__table tr:hover {
  background-color: #f5f5f5;
}
.Lingxi-loading__mask {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  width: 120px;
  height: 40px;
  background-color: rgba(0, 0, 0, 0.6);
}
.Lingxi-loading > div:nth-child(1) {
  background-color: #F38181;
  -webkit-animation: scale 0.75s -0.24s infinite cubic-bezier(0.2, 0.68, 0.18, 1.08);
          animation: scale 0.75s -0.24s infinite cubic-bezier(0.2, 0.68, 0.18, 1.08);
}
.Lingxi-loading > div:nth-child(2) {
  background-color: #FCE38A;
  -webkit-animation: scale 0.75s -0.12s infinite cubic-bezier(0.2, 0.68, 0.18, 1.08);
          animation: scale 0.75s -0.12s infinite cubic-bezier(0.2, 0.68, 0.18, 1.08);
}
.Lingxi-loading > div:nth-child(3) {
  background-color: #95E1D3;
  -webkit-animation: scale 0.75s 0s infinite cubic-bezier(0.2, 0.68, 0.18, 1.08);
          animation: scale 0.75s 0s infinite cubic-bezier(0.2, 0.68, 0.18, 1.08);
}
.Lingxi-loading > div {
  width: 25px;
  height: 25px;
  border-radius: 100%;
  margin: 5px;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  display: inline-block;
}
.Lingxi-MinLoading > div {
  width: 5px;
  height: 30px;
  border-radius: 2px;
  margin: 2px;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  display: inline-block;
  -webkit-animation: line-scale-pulse 0.9s -0.6s infinite cubic-bezier(0.85, 0.25, 0.37, 0.85);
          animation: line-scale-pulse 0.9s -0.6s infinite cubic-bezier(0.85, 0.25, 0.37, 0.85);
}
.Lingxi-MinLoading > div:nth-child(3) {
  background-color: #95E1D3;
}
.Lingxi-MinLoading > div:nth-child(2),
.Lingxi-MinLoading > div:nth-child(4) {
  background-color: #FCE38A;
  -webkit-animation-delay: -0.4s !important;
          animation-delay: -0.4s !important;
}
.Lingxi-MinLoading > div:nth-child(1),
.Lingxi-MinLoading > div:nth-child(5) {
  background-color: #F38181;
  -webkit-animation-delay: -0.2s !important;
          animation-delay: -0.2s !important;
}



